<template>
    <div class="mainsee">
	<v-head></v-head>
    <div id="context"> 
       <el-table
      :data="newsList"
	  border
      style="width: 100%">
      <el-table-column
       prop="courseName"
        label="课程名"
        min-width="33%">
      </el-table-column>
      <el-table-column
        prop="stuid"
        label="ID"
        min-width="33%">
      </el-table-column>
	  <el-table-column
	    prop="name"
        label="名字"
        min-width="33%" >
		
      </el-table-column>
      <el-table-column
	    prop="score"
        label="分数"
		min-width="54%">
      </el-table-column>
    </el-table>
	<div class="mainbutton">
      <el-button type="primary" @click="back()">返回</el-button>
     </div>    
    </div>
    </div>
</template>

<script>
     import vHead from '../commons/TeacherNav.vue';
    export default {
		components:{
        vHead,
    },
    created(){
        this.courid=localStorage.getItem("selectCourse");
        var vm=this;
        $.ajax({
            type:'post',
            dataType:'json',
            url:'/teacher/getStudentTakes',
            data:{
                "couID":this.courid
            },
            success:function(response){
                if(response.string!=""){
                    console.log("-->",response);
                    vm.analysisData(vm,response.string);
                    console.log("结束");
                }

            },
            error:function(err){
                console.log(err);
                console.log("error");
            }
        })



    },
        data: function(){
            return {
            courid:'',
            hostURL:"/",
            newsList: []

            }
        },
        methods:{
            getnewsList(id){//从后端获取数据，id表示获取哪个班的数据
                var self = this;
               // self.newsList = [];
            },
           back(){
            self=this;
             localStorage.setItem("openClass",'1');
            console.log(localStorage)
		    self.$router.push('/teacher/registerScore');
           },
           

        analysisData(vm,responseData){
        var arraya=responseData.split(';')
        
            console.log(arraya.length)
            for(var i=0;i<arraya.length;i++){
            var ele=arraya[i].split(',');
            var dist=new Array();
            for(var j=0;j<ele.length;j++){
                var eleItem=ele[j].split(':')
                console.log(eleItem[0],'->',eleItem[1])
                dist[eleItem[0]]=eleItem[1]
                }
         
            vm.newsList.push(dist);
            }
            console.log(vm.newsList)
        },

        },
        
    }
</script>

<style scoped>
.mainsee{
 
  }
  #context{
    margin:0 auto;
    width:98%;
    background-color:#eef1f6;
    padding:10px;
}
.mainbutton{
    margin-top:50px;
	margin-left:0px;
}
.el-table{
text-align:left;
}
</style>


